// main container
.user__container{
  flex-direction: column;
  align-items: center;
  row-gap: 50px;
// inner wrap box
  .user__list{
    justify-content: center;
    gap: 29px;
  }
  .btn{
    width: 120px;
  }
// title
  h2{
    margin-top: 140px;
  }
// media query
  .user__list{
    @media (max-width: 768px) {
      gap: 16px;
    }
  }
}